.coin-container .content {
    max-width: 740px;
    margin: 1rem auto;
    padding: .7rem 1rem;
    display: flex;
    flex-direction: column;
    background-color: #26272b;
    box-shadow: 0px 0px 12px #18181b;
    border-radius: 8px;
    color: #f4f4f4;
}

body.light-mode .coin-container .content {
    background-color: #ffffff;
    box-shadow: 0px 0px 12px #d4d4d4;
    color: #000000;
}

.rank {
    margin: .5rem 0;
}

.rank-btn {
    border: 1px solid #ffcc00;
    box-shadow: 0px 0px 8px #ffcc00;
    background-color: #9e7f02;
    border-radius: 8px;
    padding: .2rem;
    color: #ffffff;
}

body.light-mode .rank-btn {
    background-color: #ffcc00;
    color: #000000;
}

.info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.info .coin-heading {
    display: flex;
    align-items: center;
    margin: 1rem 0;
}

.info .coin-price {
    display: flex;
    align-items: center;
    justify-content: center;
}

.info p {
    padding-right: 1rem;
}

.table {
    margin: .5rem 0;
}

td, th {
    padding: 8px;
    text-align: center;
}

th {
    background-color: #333;
    color: #f4f4f4;
}

body.light-mode th {
    background-color: #f4f4f4;
    color: #333;
}

.stats {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    width: 100%;
}

.stats .row {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #808080;
    margin: .6rem 0;
    padding-bottom: .5rem;
}

.about h3 {
    margin: 1rem;
}

@media screen and (max-width: 700px) {
    .coin-container .content {
        max-width: 100%;
        margin: .5rem;
        padding: 0 0.5rem;
    }

    td, th {
        padding: 3px;
    }
}